import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController, Events } from 'ionic-angular';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { Storage } from '@ionic/storage';
import { LocalNotifications } from '@ionic-native/local-notifications';
import * as moment from 'moment';
/**
 * Generated class for the SettingsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {
    
    public user:any;
    public password:any;
    public properties:any;
    public settings:any;

    constructor(public navCtrl: NavController, public navParams: NavParams, public events: Events, public authProvider: AuthenticationProvider, public storage: Storage, private alertCtrl: AlertController, private localNotifications: LocalNotifications) {

        this.user = {appUserName: "", email:"", mobile:"", password:""};
        this.settings = {sound:"sound"};
        
        
        this.properties = {loading:false, error:""}  
        
        this.storage.get("user").then((data) => {
            if (data){
                console.log(data);
                //this.user.userId = data.userId;
                this.user.appUserName = data.appUserName;
                this.user.email = data.email;
                this.user.mobile = data.mobile;
                this.user.password = "";          
            }  
        });

    }



    ionViewDidLoad() {
      console.log('ionViewDidLoad SettingsPage');
    }
    
    
    public save(){
        this.properties.loading = true;
        let user = JSON.parse(JSON.stringify(this.user));
        if (!user.password){delete user.password;}
        this.storage.set("settings", this.settings);
        this.events.publish("settings:updated", this.settings);
        this.authProvider.updateUser(user).then((data) => {
            this.properties.loading = false;
            let alert = this.alertCtrl.create({
              title: 'Success',
              message: 'Your user details have been updated',
              buttons: [
                {
                  text: 'OK',
                  role: 'cancel'
                }
              ]
            });
            alert.present();                 
            //popup
        }).catch((e) => {
            this.properties.loading = false;
            console.log(e);
            this.properties.error = e.error.message;
        })
    }
    
    public testNotifications(notificationType){
        let newDate =  moment().add(1, "minutes");
        let sound = null;
        if (notificationType === "Azan A"){
            sound = 'file://assets/audio/Azan-A.mp3';
        }
        else if (notificationType === "Azan S"){
            sound = 'file://assets/audio/Azan-S.mp3';
        }
        this.localNotifications.schedule({
            id:1,
            title: "Test notification",
            text: "This is a test",
            trigger: {at: new Date(newDate.year(), newDate.month(), newDate.date(), newDate.hour(), newDate.minutes())},
            smallIcon: 'res://small_icon',
            sound: sound,
            led: 'FF0000',
        });             
    }

}



